.TodoSearch {
  /* background: #262926; */
  /* border-radius: 2px; */
  border: 2px solid #20232931;
	display: flex;
  margin: 24px auto;
  height: 64px;
  width: calc(100% - 80px);
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  /* box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25); */
	border-radius: 14px;
	background: #c1d3f7;
	box-shadow:  -5px -5px 8px #5670a1,
							5px 5px 8px #7498d9;
}

.TodoSearch::placeholder {
  color: #23213d54;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #1f2044;
}
