form {
  width: 90%;
  max-width: 300px;
  background-color: #b2c4e4;
  padding: 33px 40px;
  display: flex;
  align-content: center;
  flex-direction: column;
  border-radius: 10px;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 26px;
  color: rgba(33, 29, 44, 0.781);
}
textarea {
  background-color: #F9FBFC;
  border: 2px solid #ccc;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
	resize: none;
	border-radius: 10px;
	background: #cdd5e48e;
	box-shadow:  -12px 12px 24px #7e92b9;
							/* inset 12px -12px 24px #b8d6ff; */
}

textarea::placeholder {
color: #524f4f79;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:focus {
  outline-color: #9bb4e4;
}

.TodoForm-buttonContainer {
  margin-top: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 5px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button--add {
  background-color: #61d9fab6;
}
.TodoForm-button--add:hover{
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 217, 250, 0.637);
}
.TodoForm-button--cancel {

	background: rgba(36, 18, 18, 0.356);
}
.TodoForm-button--cancel:hover{
	background: rgba(87, 4, 4, 0.37);
  box-shadow: 0px 5px 10px rgba(87, 4, 4, 0.37);

}